.song-container {
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 0rem 0rem 0rem;
}
img {
    width: 20%;
    border-radius: 50%;
}
h2 {
    padding: 3rem 1rem 1rem 1rem;
    font-weight: 700;
}
h3 {
    font-size: 1rem;
    font-weight: 400;
}

@media screen and (max-width: 768px) {
    .song-container {
        img {
            width: 68%;
        }
    }
}

