.player {
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.time-control {
    width: 50%;
    display: flex;
    align-items: center;
    input {
        width: 100%;
        -webkit-appearance: none;
        appearance: none;
        background: transparent;
        cursor: pointer;
    }
    p{
        padding: 1rem;
    }
}

    .play-control {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1rem;
        width: 30%;
        svg {
            cursor: pointer;
        }
    }
    input[type="range"]:focus {
        outline: none;
    }
    input[type="range"]::-webkit-slider-thumb {
        -webkit-appearance: none;
        height: 16px;
        width: 16px;
    }
    input[type="range"]::-moz-range-thumb {
        -webkit-appearance: none;
        appearance: none;
        background: transparent;
        border: none;
    }
    .track {
        background: lightblue;
        width: 100%;
        height: 1rem;
        position: relative;
        border-radius: 1rem;
        overflow: hidden;
    }

    .animate-track {
        background: rgb(218, 218, 218);
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0; 
        transform: translateX(0%);
        pointer-events: none;
    }

    @media screen and (max-width: 768px) {
        .time-control {
            width: 90%;
        }
        .play-control {
            width: 65%;
        }
    }